.tb_see_more_btn_wrap{
    text-align: center;
    width: 100%;
    padding: 15px 0;
    display: flex;
    align-items: center;
    justify-content: center;
}
.tb_see_more_btn {
    font-family: var(--tb-btn-font);
    padding: var(--tb-btn-padding);
    color: var(--tb-btn-color);
    background-color:  var(--tb-btn-bg-color);
    min-width:  var(--tb-btn-width);
    min-height: var(--tb-btn-height);
    border-radius: var(--tb-btn-border-radius);
    font-weight: var(--tb-btn-font-weight);
    border-color: var(--tb-btn-border-color);
    transition: var(--tb-btn-transition);
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    border-width: 1px;
    border-style: solid;
    font-size: 14px;
    line-height: normal;
    cursor: pointer;
    margin: 0 auto;
    position: relative;
}

.tb_see_more_btn:hover{
  color: var(--tb-btn-color-hover);
  background-color:  var(--tb-btn-bg-color-hover);
  border-color: var(--tb-btn-border-color-hover);
}
.tb_see_more_btn.tb_more_btn_loading{
    padding-right: 38px;
    min-width: auto;
    min-width:  var(--tb-btn-width);
    min-height: var(--tb-btn-height);
}
.tb_more_btn_loading:before {
    content: "";
    position: absolute;
    top: 50%;
    border-radius: 50%;
    width: 18px;
    height: 18px;
    margin-top: -9px;
    border: 1.5px solid var(--tb-btn-color);
    border-right: 1.5px solid transparent;
    left: auto;
    right: 12px;
    display: flex;
    -webkit-animation: tb_btn_animation-spinner 0.5s linear infinite;
    animation: tb_btn_animation-spinner 0.5s linear infinite;
}

@-webkit-keyframes tb_btn_animation-spinner {
    to {
      -webkit-transform: rotate(360deg);
      transform: rotate(360deg);
    }
  }
  
  @keyframes tb_btn_animation-spinner {
    to {
      -webkit-transform: rotate(360deg);
      transform: rotate(360deg);
    }
  }